import React, { Component } from 'react'
import { Container, Row, Col } from "react-bootstrap";
import SocialMedia from '../components/SocialMedia';

export default class Social extends Component {
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col sm={12} md={12}>
                            <div className="sosmed">
                                <SocialMedia />
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}
